.mainDivProfil {
    font-family: Arial, sans-serif;
    /* margin: 0; */
    margin: 2% auto;
    padding: 0;
    display: flex;
    justify-content: center;
    margin-top: 2%;
    width: 100vw;
    height: 50%;
}

.profile-container {
    width: 80%;
    max-width: 600px;
    border: 1px solid #ccc;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.profile-header {
    background-color: #d2d3d4;
    color: #000000;
    padding: 10px;
    text-align: center;
}

.profile-info {
    padding: 20px;
}

.profile-info img {
    cursor: pointer;
}

.profile-info img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    display: block;
    margin: 0 auto 20px;
}

.profile-info h2 {
    font-size: 24px;
    margin-bottom: 10px;
}

.profile-info p {
    font-size: 16px;
    margin-bottom: 5px;
}

.profile-info p:last-child {
    margin-bottom: 0;
}