.mainDivUserPanel {
    display: flex;
    /* justify-content: center;
    align-items: center; */
}


.logoutUser {
    display: flex;
    cursor: pointer;
    border-radius: 8px;
    width: 80%;
    justify-content: space-evenly;
    align-content: center;
    align-items: center;
    margin-top: 100%;
    background-color: #0D6EFD;
    color: #ffffff;

}

.logoutUser:hover {
    cursor: pointer;
    background-color: rgb(255, 255, 255);
    color: rgb(13, 110, 253);
    height: 30px;
}

