aside a {
    font-size: 16px;
    color: #0a0a0a;
    display: block;
    padding: 12px;
    margin-left: 20px;
    height: 40px;
    padding-bottom: 10px;
    text-decoration: none;
    -webkit-tap-highlight-color: transparent;
}

