.headerDocs {
    margin-top: 50px;
}

.pdfBtn {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    /* Create 4 equal columns */
    gap: 10px;
    /* Add some space between grid items */
    margin-bottom: 100px;
    margin-top: 50px;
    margin-right: 100px;
    justify-items: center;
    /* Center items horizontally */
}

.pdfItem {
    text-align: center;
    /* Center text */
}

.pdfBtn button {
    width: 100px;
    justify-items: center;
    align-items: center;
    /* Make buttons fill the entire grid cell */
}


.tableMainDiv {
    font-family: Arial, sans-serif;
    background-color: #f2f2f2;
    width: 100%;
    /* Set the width of the div */
    margin: 0 auto;

}


.tableDocs {
    width: 40%;
    border-collapse: collapse;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    /* margin: 20px auto; */
    margin: 2% auto;
    background-color: #fff;
}

th,
td {
    padding: 12px 15px;
    /* text-align: left; */
    border-bottom: 1px solid #ddd;
}

th {
    background-color: #547ed1;
    color: white;
}

tr:nth-child(even) {
    background-color: #f2f2f2;
}

tr:hover {
    background-color: #ddd;
}