.formPositionWrapper {
    position: absolute;
    z-index: 100;
    background-color: rgb(0, 0, 0, 0.35);
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.formWrapper {

    width: 320px;
    background-color: #1E1F21;
    color: #DDDDDD;
    box-shadow: unset;
}

/* node js force x509 algorithm rsa */
.eventTitle {
    padding: 8px 14px;
    font-size: .85rem;
    width: 100%;
    border: unset;
    background-color: rgb(235, 235, 235);
    color: #000000;
    outline: unset;
    border-bottom: 1px solid#464648;
}

.evenBody {
    padding: 8px 14px;
    font-size: .85rem;
    width: 100%;
    border: unset;
    background-color: #dee1e7;
    color: #000000;
    outline: unset;
    border-bottom: 1px solid #464648;
    resize: none;
    height: 60px;
}



.buttonsWrapper {
    padding: 8px 14px;
    display: flex;
    justify-content: flex-end;
}


.buttonWrapper {
    color: #27282A;
    border: 1px solid #27282A;
    border-radius: 5px;
    cursor: pointer;

    &:not(:last-child) {
        margin-right: 2px;
    }
}

/* typeOfDay */

.typeOfDay {
    margin-top: 12%;
    margin-left: 8%;
    margin-right: 8%;

    display: flex;
    justify-content: center;
    align-items: center;

    height: 12px;
    width: 64px;
}


.centered {
    border-radius: 10px;
    display: table-cell;
    vertical-align: middle;
    background-color: #ebeef2;
    padding: 20px;
    padding-left: 10px;
    box-shadow: 0 0 0 1px #ebe8e8, 0 8px 20px 6px #dadada;
}

.ulTypeOfDay {
    margin: 0;
    padding: 0;
}

.itemCategory {
    display: flex;
    align-items: center;
    width: 250px;

}

.radios {
    text-align: center;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    justify-content: center;
}

.descriptionDay {
    margin-left: 10px;
}