tr:nth-child(even) {
    background-color: #ffffff;
}

tr:hover {
    background-color: #f0efef;
}

.btn {
    display: inline-block;
    padding: 10px 20px;
    font-size: 14px;
    color: #fff;
    background-color: #0019d8;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    overflow: hidden;
    transition: background-color 0.5s ease;
    margin-left: 4px;
    background-color: #0D6EFD;
}

.btn:hover {
    color: #fff;
    background-color: #4cc790e2;
    border-radius: 10px;

}

