.body {
    display: grid;
    grid-template-columns: 50fr 50fr 50fr 50fr 50fr 50fr;
    grid-template-rows: 100px 100fr;
    /* background-color: #ccc8c8ab; */
    height: 100vh;
    margin: auto;
    gap: 10px;
    border-radius: 10px;
    margin-top: 1%;

}

.itemProfil {
    display: flex;
    grid-column-start: 1;
    grid-column-end: 4;
    grid-row-start: 1;
    grid-row-end: 2;
    text-align: center;
    background-color: #ffffff;
    border-radius: 10px;
    display: flex;
    align-items: center;
    align-content: center;
}

.itemDocs {
    grid-column-start: 4;
    grid-column-end: 6;
    grid-row-start: 2;
    grid-row-end: 3;
    margin: 0;
    padding: 0;

}

.itemWorkTimeMonth {
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 2;
    grid-row-end: 3;
    background-color: #ffffff;
    border-radius: 10px;
    max-height: 100px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;

}

.itemWorkTime {
    grid-column-start: 4;
    grid-column-end: 6;
    grid-row-start: 1;
    grid-row-end: 2;
    background-color: #ffffff;
    border-radius: 10px;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: space-evenly;

}


.itemWorkDaysMonth {
    grid-column-start: 1;
    grid-column-end: 4;
    grid-row-start: 2;
    grid-row-end: 3;
    margin: 0;
    padding: 0;
    width: 100%;
}



.datepicker {
    /* margin-top: 20px; */
    width: 150px;
}