.calendarGridDiv {
    width: 1000px;
    height: 500px;
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    grid-template-rows: repeat(6, 1fr);
    /* margin: 5% 0% 0% 10%; */
    background-color: #404040;
    grid-gap: 1PX;
}


