.dayShowWrapper {
    display: flex;
    flex-grow: 1;
    justify-content: center;
    align-items: center;
    border-top: 1px solid#464648;
}

.eventsListWrapper {
    background-color: #1E1F21;
    color: #DDDDDD;
    flex-grow: 1;
}

.eventFormWrapper {
    /* background-color: #576785; */
    color: #DDDDDD;
    width: 300px;
    height: 500px;
    position: relative;
    border-left: 1px solid#464648;
}

.noEventMsg {
    color: #565759;
    position: absolute;
    top: 50%;
    right: 50%;
    transform: translate(50%, -50%);
}

