}

.eventListItemWrapper {
    padding-left: 2px;
    padding-right: 2px;
    margin-bottom: 2px;
    display: flex;
}

.eventItemWrapper {
    position: relative;
    flex-grow: 1;
    /* left: -14px; */
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    width: 114px;
    border: unset;
    background: unset;
    color: #ffffff;
    cursor: pointer;
    margin: 0;
    padding: 0;
    text-align: left;
}

.modalFormBtn {
    color: #eaeaea;
    background-color: #222421e2;
    border-radius: 5px;
    position: absolute;
    right: 50%;
    transform: translate(50%, -50%);
}