.monitorMain {
    display: flex;
    justify-content: space-between;
    background-color: #454a53;
    color: #DCDDDD;
    padding: 16px;
    position: relative;
}

span {
    font-size: 32px;
}

.btnMonitor {
    border: unset;
    background-color: #565759;
    height: 28px;
    margin-right: 2px;
    border-radius: 4px;
    color: #E6E6E6;

    /* outline: unset; */
    /* background-color: #4f505149; */
}

.btnMonitorToday {
    padding-right: 16px;
    padding-left: 16px;
    font-weight: bold;
}

.btnsWrapper {
    display: flex;
    align-items: center;
}


