.todayButton {
    font-weight: bold;
}

.btnsCenterWrapper {
    display: flex;
    position: absolute;
    top: 50%;
    right: 50%;
    transform: translate(50%, -50%);
}


.btnWrapper {
    border: unset;
    /* background-color: #454a53; 27282A */
    border: 1px solid #565759;
    
    border-radius: 4px;
    /* color: #DCDDDD; a4a6a9 E6E6E6*/
    outline: unset;
    cursor: pointer;

    &:not(:last-child) {
        margin-right: 2px;
    }

    display: flex;
    justify-content: center;

}

