.btnShowModal {
    height: 100px;
    width: 200px;
    margin: auto 0;
    margin-left: 10%;
    justify-content: center;
    align-content: center;
}

.btnModal {
    background-color: rgba(44, 97, 243, 0.932);
}

.gridColorwrapper {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(1, auto);
    gap: 5px;
}

.radios {
    text-align: center;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    margin: auto 0;
    cursor: pointer;

}

.square {
    width: 50px;
    height: 18px;
    text-align: center;
    border-radius: 5px;
    margin-left: 30%;
}