.calendarWraper {
    min-width: 140px;
    min-height: 80px;
    cursor: pointer;
    /* taze goshdym */
    /*background-color: rgba(107, 121, 185, 0.986);*/
    color: aliceblue;
}

.rowInCell {
    display: flex;
    flex-direction: column;
    justify-content: end;
}

.showDayWrapper {
    display: flex;
    justify-content: flex-end;
}

.dayWrapper {
    height: 33px;
    width: 33px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;

}

.currentDay {
    height: 90%;
    width: 90%;
    background: rgba(0, 182, 152, 0.822);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.eventListWrapper {
    margin: 0;
    padding: 0;
    list-style: none;
    padding-left: 4px;
    /* margin: unset;
    list-style-position: inside;
    padding-left: 4px; */

}

.eventListItemWrapper {
    padding-left: 2px;
    padding-right: 2px;
    margin-bottom: 2px;
    display: flex;
}

.eventItemWrapper {
    position: relative;
    flex-grow: 1;
    /* left: -14px; */
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    width: 114px;
    border: unset;
    background: unset;
    color: #ffffff;
    cursor: pointer;
    margin: 0;
    padding: 0;
    text-align: left;
}


.lineWrapperItem {
    height: 3px;
    width: 100px;
    margin-left: 20px;
    margin-top: 2px;
    border-radius: 50%;
}